@import url(https://fonts.googleapis.com/css2?family=Cedarville+Cursive&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.body{
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin:0;
  padding:0;
  position: relative;
}

.body::after{
  content: "";
  background-image:url(/calm/static/media/back.d73e3b6e.jpg);
  background-size: cover;
  background-repeat: no-repeat;
  opacity:0.6;
  position: absolute;
  top:0px;
  left:0px;
  right:0px;
  bottom:0px;
  margin:0;
  z-index: -1;
  /* https://ddorang-d.tistory.com/89 해결법 1 참고*/
}

.login{
  -webkit-flex-direction: row;
          flex-direction: row;
  padding:20px;
  color:white;
  font-size:20px;
  font-weight: bold;
  text-align: right;
}

.combo{
  position: absolute;
  -webkit-flex-direction: row;
          flex-direction: row;
  margin-left: 30px;
  font-size:50px;
  font-weight: bold;
  color:beige;
}

.combo select{
  position: absolute;
  border-radius: 50px;  
  margin-left:20px;
  margin-top:13px;
  width:181px; 
  height:50px; 
  background-color: rgba(0, 0, 0, 0.3);
}

.daily{
  -webkit-flex-direction: row;
          flex-direction: row;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
  height:60px;
  width:auto;
  -webkit-align-content: center;
          align-content: center;
  background-color: rgba(0, 0, 0, 0.1);
  border-radius: 32px;
  margin-top: 80px;
  margin-bottom:20px;
  margin-left: 30px; 
  margin-right: 65px;
  padding-top:1px;
  padding-left:10px;
}

.daily_image{
  width:50px;
  height:50px;
  margin-right:20px;
  margin-left:0px;
  background-image: url(/calm/static/media/8.161ed9d4.jpg);
  border-radius: 50px;
}

a{
  text-decoration: none;
  color: white;
}

.nav{
  display: -webkit-flex;
  display: flex;
  margin-right: 20px;
  margin-bottom: 0;
  width: 300px;
  height: auto; /*item 줄이 늘어나는 거에 맞춰서 늘어나야 하니까
  여기를 고정 값이 아닌 auto로 해준다. 고정값으로 하면 아무리 늘어나도 그만큼만 늘어남*/
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-flex-wrap:nowrap;
          flex-wrap:nowrap;
  /* rgba 마지막 속성이 배경의 투명도 결정*/
  background-color: rgba(0, 0, 0, 0.7);
  font-size:20px; font-weight: bold; 
  -webkit-flex:none; 
          flex:none;
  padding-top:20px;
  /* flex:none으로 설정시 늘거나 줄지 않는다*/
  list-style:none;
} 

.nav_logo{
  display:-webkit-flex;
  display:flex;
  -webkit-flex-direction: column;
          flex-direction: column;
}

.logo{
  font-family: 'Cedarville Cursive', cursive; 
  font-size: 30px; font-weight:bold;
  padding-left:30px;
  padding-bottom: 20px;
}

.free{
  list-style:none;
  margin-left: 25px;
}

.nav_menu{
  list-style: none;
  margin-bottom: 0;
  margin-left: 30px;
  margin-top: 40px;
}

/*메뉴 아이콘*/
.nav_menu i{ 
  font-size: 29px;
  margin-right: 5px;
}

/*햄버거 버튼 span 세 개로 만듦*/
#menuicon{
  display: none;
  right: 32px; /*오른쪽에서 32px 떨어진 곳에 배치*/
  margin-top:35px;
  margin-left:0;
}

.main{
  height: 100%;
  width:auto;
  -webkit-flex:4 1;
          flex:4 1; 
  /*body 안에서 nav와 main의 공간 비율. 이정도 공간을 부여하지 않으면 세로 일렬로 나와버림*/
}

.items{
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(200px, 1fr));
  /*repeat(auto-fill, minmax(100px, 1fr)) : 5개열이 반복되고 최소100px, 최대 1fr*/
  /*auto-fit은 쭉 늘려서 row에 딱 맞추는 것*/
  /* https://blog.naver.com/bjoh1227/222025668234 참고 */
  grid-gap: 5px;
  width: auto;
  height: 100%;
  -webkit-flex-wrap:wrap;
          flex-wrap:wrap;
  margin-bottom:100px;
  margin-left:5px;
  text-align:center;
}

.items p{ /*아이템들 안의 제목들*/
  position:relative;
  top:150px;
  background-color: rgba(0,0,0,0.5);

  color:white;
