@media screen and (max-width: 800px){
  /* 로고는 row 정렬, nav는 column 정렬*/

  .body{
      -webkit-flex-direction: column;
              flex-direction: column;
      margin:0;
      width:100%;
  }
  
  .nav{
      display: -webkit-flex;
      display: flex;
      -webkit-flex-direction: column;
              flex-direction: column; /*감춰진 menu까지 정렬하는 것을 생각해야 함*/
      width:100%;
      padding-bottom: 10px;
      margin-bottom: 20px;
  }
  
  .nav_logo{
      display:-webkit-flex;
      display:flex;
      -webkit-flex-direction: row;
              flex-direction: row; /*안에 있는 세개 요소 가로 정렬*/
      padding-right:0px;
      margin-right: 0px;
  }

  .logo{
      display: -webkit-flex;
      display: flex;
      margin-left: -10px;
      margin-top: 20px;
      -webkit-flex:1 1;
              flex:1 1;
      -webkit-align-self: center;
              align-self: center;
  }
  .free{
      display: -webkit-flex;
      display: flex;
      padding-top: 0px;
      margin-right: 95px;
      margin-left:5px;
      -webkit-align-self: center;
              align-self: center;
      -webkit-flex:1 1;
              flex:1 1;
  }

  #menuicon + label {/* +는 인접요소 선택자*/
      display:-webkit-flex;
      display:flex;
      position:relative;
      -webkit-flex:0.35 1;
              flex:0.35 1;
      margin-top:37px;
      margin-bottom:36px;
      cursor:pointer;
  }
  #menuicon + label span{
      display: block;
      position: absolute;
      width:28px;
      height:4px;
      border-radius: 30px;
      background:white;
      transition:all .35s;
  }
  #menuicon + label span:nth-child(1){
      top:0;
  }
  #menuicon + label span:nth-child(2){
      top:50%;
      -webkit-transform:translateY(-50%);
              transform:translateY(-50%);
  }
  #menuicon + label span:nth-child(3){
      bottom:0;
  }

  #menuicon:checked + label span:nth-child(1){
      top:50%;
      -webkit-transform:translateY(-50%) rotate(42deg);
              transform:translateY(-50%) rotate(42deg);
  }
  #menuicon:checked + label span:nth-child(2){
     opacity:0;
  }
  #menuicon:checked + label span:nth-child(3){
      bottom:36%;
      -webkit-transform:translateY(-50%) rotate(-42deg);
              transform:translateY(-50%) rotate(-42deg);
  }

  .nav_menu {
      display: none;
      -webkit-flex-direction: column;
              flex-direction: column;
      border:top 1px white;
      text-align: center;
      margin-bottom:20px;
      -webkit-flex-wrap: wrap;
              flex-wrap: wrap;
      font-size:20px;
      padding:0;
  }

  .login{
      display: none;
  }

  .combo{
      padding-left:25px;
  }

  #all{
      margin-top:10px;
      margin-left:10px;
  }

  .daily{
      margin-top:80px;
      margin-left:50px;
      margin-right:50px;
      width:auto;
      -webkit-flex:auto;
              flex:auto;
  }

  .items{
      margin-left:25px;
  }

  .nav_menu.active {
      display: -webkit-flex;
      display: flex;  
  }
}

